<template>
    <div class="content">
        <el-page-header @back="$router.back()" content="实名认证二维码" />
        <div class="qrbody">
            <el-alert
                    class="alert"
                    title="认证结束后请重新进入本页面查询认证状态"
                    type="warning"
                    :closable="false"
                    show-icon>
            </el-alert>
            <h3 v-if="qrtext==''">抖音号不存在，请返回重试</h3>
            <h3 v-else-if="qrtext=='已认证'">此抖音号已认证，无法继续认证</h3>
            <template v-else>
                <vue-qr :text="qrtext" :size="220" :correctLevel="3" :margin="8" class="qrimg"></vue-qr>
                <div class="hint">请使用<b>支付宝</b>进行扫码</div>
                <el-popover
                        placement="top"
                        width="200"
                        v-model="visible">
                    <p>标记后您可以继续恢复抢单限额，并且可以次日继续完成本单，是否继续？</p>
                    <div style="text-align: right; margin: 0">
                        <el-button size="mini" type="text" @click="visible = false">取消</el-button>
                        <el-button type="primary" size="mini" @click="delay">确定</el-button>
                    </div>
                    <el-button slot="reference" type="primary" plain size="small" style="margin-top: 40px;">标记异常，次日认证</el-button>
                </el-popover>
            </template>
        </div>
    </div>
</template>

<script>
import request from "@/request";
import VueQr from 'vue-qr';

export default {
    name: "Qrverify",
    metaInfo: {
        title: '实名认证二维码',
    },
    components: {
        VueQr,
    },
    beforeMount() {
        request.Douyin.status(this.$route.params.id).then(res=>{
            if (res.status > 0) {
                if(res.data.verify > 0) {
                    this.qrtext = '已认证';
                }else{
                    this.qrtext = window.location.origin + '/verify/' + res.data.account;
                }
            }else{
                this.qrtext = '';
            }
        });
    },
    data: function () {
        return {
            qrtext: '正在加载中...',
            data: '',
            visible: false
        };
    },
    methods: {
        delay: function () {
            this.visible = false;
            console.log(this.$route.params.id)
            request.Account.delay(this.$route.params.id).then(res=>{
                if (res.status > 0) {
                    this.$message({
                        message: '操作成功！',
                        type: 'success'
                    });
                }else{
                    this.$message.error('操作失败，请重试！');
                }
            });
        }
    }
}
</script>

